@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
:root {
  --bg-color: #f2d492;
  --primary-color: #746545;
  --secondary-color: #f29559;
  --font-color: #202c39;
  --accent-color: #ffe3a6;
}
* {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
  /* border: 2px solid black; */
}
*::selection {
  background-color: var(--secondary-color);
  color: var(--font-color);
}
body {
  background-color: var(--bg-color);
  color: var(--font-color);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-style: normal;
  text-align: center;
  padding: 10px;
}
.App{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 700px;
  margin-top: 20px;
}
.App > h1{
  margin-bottom: 30px;
  box-shadow:  7px 7px 9px #ceb47c,  -7px -7px 9px #fff4a8;
} 
.gpaBox {
  width: 350px;
  min-height: 450px;
  padding: 50px 20px 20px 20px;
  display: flex;
  flex-direction: column; 
  justify-content: flex-start;
  align-items: center;

  border-radius: 50px;
  background: var(--bg-color);
  box-shadow: 29px 29px 61px #dabf83, -29px -29px 61px #ffe9a1;
}
.h1Box {
  padding: 20px;
  border-radius: 50px;
  font-weight: 900;
  background: var(--bg-color);
  box-shadow: 7px 7px 14px #d3b87f, -7px -7px 14px #fff0a5;
}
.h2Box {
  padding: 10px;
  border-radius: 50px;
  font-weight: 900;
  margin-top: 20px;
  width: 50%;
  background: var(--bg-color);
  box-shadow: 7px 7px 14px #d3b87f, -7px -7px 14px #fff0a5;
}
form {
  width: 100%;
  margin: 20px 0px;
}
.row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  /* border: 2px solid black; */
  margin: 10px 0px;
}
input,
button {
  background: none;
  border: none;
  outline: none;
  color: var(--primary-color);
  font-weight: 900;
  width: 100%;
  font-size: 16px;
  text-align: center;
}
input::placeholder{
  font-weight: 500;
  font-size: 10px;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}


.thBox {
  padding: 10px;
  border-radius: 50px;
  font-weight: 700;
  font-size: 16px;
  background: var(--bg-color);
  box-shadow: 2px 2px 4px #ceb47c, -2px -2px 4px #fff4a8;
}
.tdBox {
  padding: 10px;
  border-radius: 50px;
  font-weight: 700;
  font-size: 16px;
  background: var(--accent-color);
  box-shadow: inset 2px 2px 4px #ceb47c, inset -2px -2px 4px #fff4a8;
}
.btnBox {
  padding: 10px;
  border-radius: 50px;
  font-weight: 700;
  font-size: 16px;
  background: var(--b-color);
  box-shadow: 2px 2px 4px #ceb47c, -2px -2px 4px #fff4a8;
}
.btnBox:hover{
  cursor: pointer;
  background: var(--accent-color);
  box-shadow: inset 2px 2px 4px #ceb47c, inset -2px -2px 4px #fff4a8;
}
.t1 {
  width: 118px;
}
.t2 {
  width: 72px;
}
.t3 {
  width: 62px;
}
.btn {
  width: 290px;
  color: var(--font-color);
}
.addRemoveBtn{
  width: 135px;
  color: var(--font-color);
}